                <path name="麦当劳" id="svg_14" d="m129.6823,139.68275l39.6826,0l0,34.12704l-39.6826,0l0,-34.12704z" stroke-opacity="null" stroke-linecap="null" stroke-linejoin="null" stroke-width="1.5" stroke="null" fill="#bf005f" />
